<template>
  <div>
    <h1>Getting Ready for the Big Day</h1>
    <ul
      class="gettingReady-list"
      v-for="gettingready in gettingReadyData"
      v-bind:key="gettingready.id"
    >
      <li class="getting-ready">
        <img class="gettingready-img" :src="gettingready.image_url" alt="" />
      </li>
    </ul>
    <footer>&copy; 2019</footer>
  </div>
</template>

<script>
export default {
  name: 'GettingReadyForTheBigDay',
  components: {},
  data() {
    return {
      gettingReadyURL: 'postgresql:///wedding_site',
      gettingReadyData: []
    }
  },
  mounted() {
    fetch(this.gettingReadyURL, {
      method: 'get',
      mode: 'cors',
      headers: new Headers({ 'Content-Type': 'application/json' })
    })
      .then(resp => resp.json())
      .then(resp => {
        console.log(resp.gettingready)
        this.gettingReadyData = resp.gettingready
      })
  }
}
</script>

